import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { articles } from './article-content'
import { NotFoundPage } from './NotFoundPage'

// const articles = await fetch('localhost:8000/api/articles/learn-react')


export const ArticlePage = () => {

    const [upVotes, setUpVotes] = useState(0)
    const [comments, setComments] = useState([])
    
    const {name} = useParams()
    const matchingArticles = articles.find(it => it.name === name)

    useEffect(() => {
            const loadArticleInfo = async () => {
            const response = await fetch(`/api/articles/${name}`)
            console.log(response)
            const articleInfo = await response.json()
            setUpVotes(articleInfo.upVotes)
            setComments(articleInfo.comments)
        }
        
        loadArticleInfo()
    }, [name])

    const upVoteArticle = async () => {
        const response = await fetch(`/api/articles/${name}/upVotes`, {
            method: 'post',
        })

        const updatedArticleInfo = await response.json()
        setUpVotes(updatedArticleInfo.upVotes)
        setComments(updatedArticleInfo.comments)
    }

 
   return matchingArticles ? (
        <>

            <h1>{matchingArticles.title}</h1>
            <div id="upvotes-section">
                <button onClick={upVoteArticle}>Add Upvote</button>
                <p>This article has {upVotes} votes</p>
            </div>

            {matchingArticles.content.map(it => (
                <p key={it}>{it}</p>
            ))}
        </> 
    ) : (
        <NotFoundPage />
    )

}